.custom {
    background: #000;
    padding: 20px;
    min-height: 600px;
    margin-bottom: 100px;
}
.custom .content {
    color: #fff !important;
}
.custom .box {
    box-shadow: 10px 10px 10px -10px
     rgb(255, 255, 0),
      inset 10px 10px 10px -10px
      rgb(255, 255, 0);
    padding: 10px;
    color: #fff;
    text-align: center;
}
.custom .box:hover {
    background: var(--main);
    color: #000;
    cursor: pointer;
    transform: scale(1.07);
}
.custom .box h6 {
    font-size: 38px;
    font-weight: 700;
    margin: 10px auto;
}